

import React from 'react';

const Modal = () => {
    return (
        <>
         <div style={displayBlock}>
              <div style={modalbox}>
              <div style={textBox}>
              <img style={imgD} src={img} alt="success"/>
              <p style={titleEelm}>{title}</p>
              {text}
              </div>
              <button style={closeOne}  onClick={() => setShow(false)}>
                  X
              </button>
            </div>
        </div>
         </>
    );
};

export default Modal;